___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --primary: #3a86ff;
  --bg: #f0f4f8;
  --text: #222;
  --label: #555;
  --card-bg: #fff;
  --card-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  --radius: 12px;
}

body.dark {
  --bg: #1e1e1e;
  --text: #f5f5f5;
  --label: #ccc;
  --card-bg: #2a2a2a;
  --card-shadow: 0 4px 20px rgba(0, 0, 0, 0.4);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: system-ui, sans-serif;
  background-color: var(--bg);
  color: var(--text);
}

header, footer {
  background-color: #333;
  color: #fff;
  padding: 1rem;
  text-align: center;
  flex-shrink: 0;
}

main {
  flex: 1;
  width: 100%;
  padding: 2rem 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

#app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

#weatherContainer {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  background-color: var(--card-bg);
  color: var(--text);
  padding: 2rem;
  border-radius: var(--radius);
  box-shadow: var(--card-shadow);
  max-width: 600px;
  margin: auto;
}

#weatherContainer h2 {
  margin: 0 auto 1.5rem auto;
  color: var(--primary);
  font-size: 2rem;
  text-align: center;
  border-bottom: 2px solid var(--primary);
  padding-bottom: 0.5rem;
}

.weather-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 10px;
}

.label {
  font-weight: 600;
  color: var(--label);
  display: inline-block;
  min-width: 150px;
}

.day-block {
  background-color: var(--card-bg);
  border-radius: var(--radius);
  box-shadow: var(--card-shadow);
  padding: 1rem 1.5rem;
}

.day-block h3 {
  margin-top: 0;
  color: var(--primary);
}

#toggleTheme{
  width: 2rem;
  height: 2rem;
}

@media (min-width: 768px) {
  main {
    max-width: 100%;
    margin: 0 auto;
  }
}

@media (max-width: 768px) {
  .weather-grid {
    grid-template-columns: 1fr;
  }
}
